* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #c3c3c3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

header {
  background-color: #0f0f0f;
  width: 100vw;
  position: absolute;
  top: 0;
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
}

header p {
  color: #c3c3c3;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 2em;
}

footer {
  background-color: #0f0f0f;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  height: 2.5em;
}

footer p {
  color: #c3c3c3;
  font: 0.8em;
}

.showWinner {
  width: 30em;
  background: #0f0f0f;
  height: 20em;
  position: absolute;
  border: #c3c3c3 0.2em solid;
  box-shadow: 0 0 0.5em 0.15em #c3c3c3;

}

.showWinner p {
  color: #c3c3c3;

}

.game {
  background: #0f0f0f;
  display: flex;
  border-radius: 1em;
  width: 50em;
  justify-content: center;
}

.status {
  color: #c3c3c3;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-weight: 600;
  display: flex;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-size: 1.4em;
  text-align: center;
  margin-bottom: 0.8em;
}

.gameBoard {
  padding: 1em;
  margin-bottom: 0.5em;
}

.rowContainer {
  display: flex;
  height: auto;
  background-color: #c3c3c3;
}

.buttonSquare {
  width: 10em;
  height: 10em;
  background: #0f0f0f;
  margin: 0.1em;
}

.buttonImageSquare {
  width: 5em;
}

.gameInfo {
  width: 30em;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.gameInfo ul {
  margin-left: 1em;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 1.75em;
  margin-top: 4.9em;
}

.infoItem {
}

.infoItem p {
  color: #595959;
  letter-spacing: 0.1em;
  font-family: monospace;
  font-weight: bold;
  text-transform: uppercase;
}

.boton {
  background: none;
  border: none;
  outline: 1em solid red;
}